l riesgo de crédito"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La política monetaria influye en la evaluación del riesgo de crédito, tanto para los prestamistas como para los prestatarios. Cuando el banco central ajusta las tasas de interés, afecta la disponibilidad y el costo del crédito, lo que cambia la manera en que las instituciones financieras evalúan los riesgos asociados con la concesión de préstamos. Las tasas de interés altas pueden aumentar el riesgo de impago de los deudores, lo que lleva a los prestamistas a ser más cautelosos y exigir garantías más estrictas. Por otro lado, en un ambiente de tasas de interés bajas, los prestamistas pueden estar más dispuestos a asumir riesgos mayores, lo que podría aumentar la disponibilidad de crédito, pero también podría generar riesgos adicionales si no se evalúan adecuadamente las capacidades de pago de los deudores.")])]),_c('div',{attrs:{"numero":"4","titulo":"Evaluación del riesgo del sector económico"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Además de evaluar el riesgo de cada deudor individual, los bancos también consideran el sector económico al que pertenece el prestatario. La política monetaria influye en este aspecto, puesto que un entorno de altas tasas de interés, puede afectar de manera diferente, a sectores como la construcción, la tecnología, o la industria, dependiendo de su dependencia del financiamiento externo. Por ejemplo, en una situación de política monetaria restrictiva, los sectores más sensibles a las tasas de interés, como la construcción o el consumo de bienes duraderos, pueden ver disminuida su demanda, mientras que otros sectores más estables, como la energía o los bienes básicos, podrían estar menos afectados. De esta forma, los cambios en la política monetaria pueden modificar la evaluación del riesgo sectorial y, por lo tanto, influir en las decisiones de inversión y préstamo en estos sectores.")])]),_c('div',{attrs:{"numero":"5","titulo":"Expectativas de precios y actividad económica"}}